export const DynamicIcon: React.FC<IDynamicIconProps> = ({ svg }) => {
  switch (svg) {
    case "playmarket":
      return <IconPlaymarket />;
    case "link":
      return <IconLink />;
    case "github":
      return <IconGithub />;
    default:
      return <IconLink />;
  }
};
